import { UserType } from "../HW8";

const SORT_STATE = "sort";
const CHECK_STATE = "check";



type PayLoadType = "up" | "down"


type SortStateActionType = {
    type: typeof SORT_STATE
    payload: PayLoadType
}

type CheckStateActionType = {
    type: typeof CHECK_STATE
    payload: number
}

type ActionType = SortStateActionType | CheckStateActionType




export const homeWorkReducer = (state: UserType[], action: ActionType): UserType[] => { // need to fix any
    switch (action.type) {
        case SORT_STATE: {
            const newState = [...state];
            switch (action.payload) {
                case 'up':
                    newState.sort((u1, u2) => {
                        return u1.name > u2.name ? 1 : -1
                    });
                    break;
                case 'down':
                    newState.sort((u1, u2) => {
                        return u1.name > u2.name ? -1 : 1;
                    })

            }
            return newState
        }
        case CHECK_STATE: {
            return state.filter(user => user.age >= action.payload)
        }
        default:
            return state
    }
}